import React from 'react';

import Scene from './Components/Background/background';
import SceneMobile from './Components/Background/background-mobile';
import Tracks from './Components/Tracks/tracks';
import About from './Components/About/about';
import Timeline from './Components/Timeline/timeline';
import Sponsors from './Components/Sponsors/sponsors';
import Prizes from './Components/Prizes/prizes'

class App extends React.Component{
  componentDidMount(){
    let hidden = document.querySelectorAll('.hidden');
    window.addEventListener('scroll', () => {
      for(let i=0;i<hidden.length;i++){
        let positionTop = hidden[i].getBoundingClientRect().top;
         if(positionTop - window.innerHeight <=-50){
          hidden[i].classList.add('come-in-side');
         }
      }
  })
  }
  render(){
    return (/iPhone|iPad|iPod|Android/i.test(navigator.userAgent)) ? (
      <div className="App">
        <SceneMobile />
        <About />
        <Tracks />
        <Timeline />
        <Sponsors />
        <Prizes/>
      </div>
    ) : (<div className="App">
      <Scene />
      <About />
      <Tracks/>
      <Timeline />
      <Sponsors />
      <Prizes />
    </div>);
  }
  
}

export default App;
